<template>
  <div>
    <Header></Header>
    <div class="w">
      <otherPersonIntro :userObj="user" :fans="fans" :follows="follows" />
      <myFollow :myFollows="follows" v-if="this.follows.length>0"/>
      <MyFans :myFans="fans"  v-if="this.fans.length>0"/>
      <personLikeSongs :likeSongs="likeSongs"  v-if="this.likeSongs.length>0"/>
      <personLikeAlbum :songList="personalbums" :obj="albumObj" v-if="this.personalbums.length>0"></personLikeAlbum>
      <likeList :songList="personList" :obj="songObj" v-if="this.personList.length>0"></likeList>
      <!-- 我的歌单 -->
       <!-- <UserSongList :songList="personSongs" :obj="songListObj" class="ma-bt" v-if="this.personSongs.length>0"></UserSongList> -->
      <personList :songList="personSongs" :obj="songObj" class="ma-bt" v-if="this.personSongs.length>0"></personList>
      <albumFooter />
    </div>
  </div>
</template>

<script>
// import personalIntro from "../components/personal-info/personalIntro";
import otherPersonIntro from "../components/otherPerson/otherPersonIntro";
import myFollow from "../components/personal-info/myFollow.vue";
import Header from "../components/Header";

import personLikeSongs from "../components/otherPerson/personLikeSongs";
import personLikeAlbum from "../components/otherPerson/personLikeAlbum";
import likeList from "../components/otherPerson/likeList";
import personList from "../components/otherPerson/personList";

import MyFans from "../components/personal-info/MyFans";
import albumFooter from "../components/albumFooter";
// import LikeSongList from "../components/personal-info/LikeSongList";
// import UserSongList from '../components/user/userSongList'
// import personalIntro from '../components/personal-info/personalIntro';
// import LikeAlbum from "../components/personal-info/LikeAlbum";
import albumImg from "../img/矢量图/专辑，光盘.png";
import songImg from "../img/矢量图/歌单.png";
// import MySongList from "../components/personal-info/MySongList";
import axios from "axios";

export default {
  name: "person",
  components: {
    // personalIntro,
    otherPersonIntro,
    myFollow,
    Header,
    personLikeSongs,
    MyFans,
    personLikeAlbum,
    personList,
    albumFooter,
    likeList,
    // UserSongList
  },
  data() {
    return {
      personSongs: [],
      personalbums: [],
      personList: [],
      albumObj: {
        title: "喜欢的专辑",
        img: albumImg,
      },
      songObj: {
        title: "喜欢的歌单",
        img: songImg,
      },
      songListObj: {},
      user: {},
      fans: [],
      follows: [],
      likeSongs: [],
    };
  },
  created() {
    // 请求用户个人数据
    console.log(this.$route.query.id,'person id');
    axios.get(`/itmusic/user/detail?id=${this.$route.query.id}`).then((res) => {
      // console.log(res.data);
      this.user = res.data.user;
      this.fans = res.data.fans;
      this.follows = res.data.follows;
      console.log(res.data,"person data");
    });
    //请求用户喜欢的歌
    axios.get(`/itmusic/user/songs?id=${this.$route.query.id}`).then((res) => {
      this.likeSongs = res.data.like_songs;
      console.log(this.likeSongs,"父组件喜欢的歌曲");
      // this.personSongs = res.data.like_songs;
      // console.log(this.personSongs, 1);
      // // console.log("fuck");
      // for (let i = 0; i < this.personSongs.length; i++) {
      //   this.personSongs[i].upload_time = this.personSongs[i].upload_time.slice(0, 10);
      // }
      // console.log(this.personSongs.length);
      // console.log(this.$route.query.id);
    });
    axios.get(`/itmusic/user/albums?id=${this.$route.query.id}`).then((res) => {
      this.personalbums = res.data.like_albums.slice(0, 10);
      for (let i = 0; i < this.personalbums.length; i++) {
        this.personalbums[i].upload_time = this.personalbums[i].upload_time.slice(0, 10);
      }
      console.log(this.personalbums,"喜欢的专辑");
      // console.log(this.personalbums[0].upload_time);
      console.log(this.personalbums, 2);
    });
    axios.get(`/itmusic/user/playlists?id=${this.$route.query.id}`).then((res) => {
      console.log(res, 4);
      this.personList = res.data.like_lists;
      // for (let i = 0; i < this.personalbums.length; i++) {
      //   let stringtime=this.personList[i].upload_time.slice(0,10);
      //   this.personList[i].upload_time = stringtime;
      // }
      console.log(this.personList, 3);
    });
    axios.get(`/itmusic/user/playlists?id=${this.$route.query.id}&type=2`).then((res) => {
      console.log(res, 4);
      this.personSongs = res.data.create_lists;
      // for (let i = 0; i < this.personalbums.length; i++) {
      //   let stringtime=this.personList[i].upload_time.slice(0,10);
      //   this.personList[i].upload_time = stringtime;
      // }
      console.log(this.personSongs, 'perosnSongs');
    });
  },
};
</script>
<style scoped>
.ma-bt {
  margin-bottom: 69px;
}
</style>
