<template>
  <div class="album-intro">
    <!-- <div class="album-intro" :style='{"background-image":"url(+'http://'+)"}'> -->
    <div class="intro-container style-white">
      <div class="ablum-info clearfix">
        <div class="cover fl">
          <img :src="'http://47.102.215.61/media/' + album.album_picture" alt="" />
        </div>
        <div class="info">
          <div class="name fs-24 fw-b fl">{{ album.album_name }}</div>
          <div class="likeAlbum" @click="collectAlbum(album.id)">
            <img
              src="static/img/喜欢.png"
              alt=""
              :class="[isCollect ? 'img' : '']"
            />
          </div>
          <div class="singer fs-18 fw-l">{{ album.singer }}</div>
          <div class="date fs-18 fw-l">{{ album.upload_time }}</div>
          <div class="intro fs-18 fw-l">
            <p class="fs-18 fw-l">{{ album.description }}</p>
          </div>
          <div class="album-perf">
            <div class="views fl">
              <p class="fs-18 style-white fl">播放</p>
              <p class="fs-24 style-green style-impact fl number">
                {{ album.play_count }}
              </p>
            </div>
            <div class="likes fl">
              <p class="fs-18 style-white fl">喜欢</p>
              <p class="fs-24 style-green style-impact fl number">
                {{ album.like_count }}
              </p>
            </div>
            <div class="comments fl">
              <p class="fs-18 style-white fl">评论</p>
              <p class="fs-24 style-green style-impact fl number">
                {{ album.comment_count }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import img from "../../../images/upload_img/图片/marek-piwnicki-JnXebVlsVrg-unsplash 拷贝.png";

// import img from "static/img/图片/marek-piwnicki-JnXebVlsVrg-unsplash 拷贝.png";
export default {
  name: "albumIntro",
  /* props: {
    albumName: String,
    description: String,
    upload_time:String,   
    singer:String,
  }, */
  data() {
    return {
      urlimg:img,
      likeAlbum: [],
      collectAlbumObj: {},
      isCollect: true,
    };
  },
  props: {
    album: Object,
  },
  methods: {
    collectAlbum(id) {
      // console.log("0", this.isCollect);
      // 封装需要传入的对象
      this.collectAlbumObj.album_id = id;
      this.collectAlbumObj.type = this.isCollect ? 2 : 1;
      // console.log("0", this.isCollect);
      axios.post("/itmusic/collect_album", this.collectAlbumObj).then((res) => {
        // console.log(res.data,"收藏专辑返回");
        if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        } else {
          this.isCollect = !this.isCollect;

          if (!this.isCollect) {
            prompt("取消收藏成功~");
          } else {
            prompt("收藏成功~");
          }
        }
      });
    },
  },
  //在渲染页面的时候判断是否已经收藏该专辑。
  created() {
    axios.get(`/itmusic/album/detail?id=${this.$route.query.id}`).then((res) => {
      console.log(res.data, "喜欢专辑初始化的东西");
      (this.isCollect = res.data.album.is_like),
        console.log(
          "2 本地逻辑变量",
          this.isCollect,
          "2 接口逻辑变量",
          res.data.album.is_like
        );
    });
    console.log(this.album, "子组件专辑");
  },
};
</script>
<style scoped>
.img {
  transform: translateY(-24px);
  filter: drop-shadow(#659274 0 24px);
}
.album-intro {
  width: 100%;
  background-color: cornsilk;
  background: url("../../../images/upload_img/图片/marek-piwnicki-JnXebVlsVrg-unsplash 拷贝.png"),
    no-repeat;
  background-size: 100% 100%;
}
.intro-container {
  width: 1314px;
  margin: 0 auto;
  height: 423px;
  padding-top: 72px;
}
.ablum-info .cover {
  width: 346px;
  height: 346px;
  margin-left: 61px;
  margin-right: 77px;
  border-radius: 173px;
  background-color: pink;
}
.ablum-info .cover img {
  width: 346px;
  height: 346px;
  border-radius: 173px;
}
.ablum-info .name {
  margin-right: 13px;
}
.ablum-info .likeAlbum {
  width: 30px;
  overflow: hidden;
  /* height: 24px; */
  margin: 6px 0px 6px;
  cursor: pointer;
}
.ablum-info .likeAlbum img {
  width: 28px;
  height: 24px;
  /* transform: translateY(-24px);
  filter: drop-shadow(#659274 0 24px); */
}
.ablum-info .info {
  width: 1175px;
  height: 163px;
  padding-top: 60px;
}
.info .singer {
  text-align: left !important;
  text-align: center;
}
.ablum-info .info .date {
  margin-top: 8px;
}
.ablum-info .info .album-perf {
  margin-top: 15px;
}
.ablum-info .info .album-perf > div {
  margin-right: 26px;
}
.views,
.likes,
.comments :first-child {
  line-height: 36px;
}
.number {
  margin-left: 10px;
}
</style>
