<template>
  <div class="album-intro">
    <div class="intro-container style-white">
      <div class="ablum-info clearfix">
        <div class="cover fl">
          <img :src="'http://47.102.215.61/media/' + userObj.avatar" alt="" />
        </div>
        <div class="info fl">
          <div class="name fs-24 fw-b fl">{{ userObj.username }}</div>

          <!-- button modify -->
          <button
            id="modifyBtn"
            type="button"
            class="btn btn-outline-secondary btn-sm"
            data-toggle="modal"
            data-target="#modify"
          >
            <i class="bi bi-pencil-square"></i>
          </button>

          <!-- modify modal -->
          <div
            id="modify"
            class="modal fade"
            tabindex="-1"
            role="dialog"
            aria-labelledby="modalTitle"
            aria-hidden="true"
          >
            <!-- modal dialog -->
            <div class="modal-dialog modal-dialog-centered" role="document">
              <!-- modal content -->
              <div class="modal-content">
                <!-- modal header -->
                <div class="modal-header">
                  <h5 id="modalTitle" class="modal-title">Change personal information</h5>

                  <!-- button close modal -->
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>

                <!-- modal body -->
                <div class="modal-body">
                  <form id="modifyForm">
                    <!-- username input -->
                    <div class="form-group">
                      <label for="username">Username</label>
                      <input
                        id="username"
                        class="form-control"
                        type="text"
                        placeholder="Enter username here..."
                      />
                    </div>

                    <!-- age input -->
                    <div class="form-group">
                      <label for="age">Age</label>
                      <input
                        id="age"
                        class="form-control"
                        type="number"
                        placeholder="Enter your age here..."
                      />
                    </div>

                    <!-- gender input -->
                    <div class="form-group">
                      <label for="gender">Gender</label>
                      <!-- gender select -->
                      <select id="gender" class="custom-select">
                        <option value="0">Male</option>
                        <option value="1">Female</option>
                        <option value="2">Unknown</option>
                      </select>
                    </div>

                    <!-- address input -->
                    <div class="form-group">
                      <label for="address">Address</label>
                      <input
                        id="address"
                        class="form-control"
                        type="text"
                        placeholder="Enter your address here..."
                      />
                    </div>

                    <!-- description textarea -->
                    <div class="form-group">
                      <label for="description">Description</label>
                      <textarea
                        id="description"
                        class="form-control"
                        rows="3"
                        placeholder="Enter description here..."
                      ></textarea>
                    </div>

                    <!-- avatar input -->
                    <div class="form-group">
                      <label for="avatar">Avatar</label>
                      <input id="avatar" class="form-control" type="file" />
                    </div>
                  </form>
                </div>

                <!-- modal footer -->
                <div class="modal-footer">
                  <!-- button close modal -->
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    data-dismiss="modal"
                  >
                    Close
                  </button>
                  <!-- button confirm create -->
                  <button @click="modify" type="button" class="btn btn-primary">
                    Save changes
                  </button>
                </div>
              </div>
            </div>
          </div>

          <!-- button change password -->
          <button
            id="passwordBtn"
            type="button"
            class="btn btn-outline-secondary btn-sm ml-3"
            data-toggle="modal"
            data-target="#password"
          >
            <i class="bi bi-key"></i>
          </button>

          <!-- password modal -->
          <div
            id="password"
            class="modal fade"
            tabindex="-1"
            role="dialog"
            aria-labelledby="modalHeading"
            aria-hidden="true"
          >
            <!-- modal dialog -->
            <div class="modal-dialog modal-dialog-centered" role="document">
              <!-- modal content -->
              <div class="modal-content">
                <!-- modal header -->
                <div class="modal-header">
                  <h5 id="modalHeading" class="modal-title">Change password</h5>

                  <!-- button close modal -->
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>

                <!-- modal body -->
                <div class="modal-body">
                  <form id="passwordForm">
                    <!-- old password input -->
                    <div class="form-group">
                      <label for="old">Old password</label>
                      <input
                        id="old"
                        class="form-control"
                        type="password"
                        placeholder="Enter your old password here..."
                      />
                    </div>

                    <!-- new password input -->
                    <div class="form-group">
                      <label for="new">New password</label>
                      <input
                        id="new"
                        class="form-control"
                        type="password"
                        placeholder="Enter your new password here..."
                      />
                    </div>

                    <!-- again password input -->
                    <div class="form-group">
                      <label for="again">Confirm password</label>
                      <input
                        id="again"
                        class="form-control"
                        type="password"
                        placeholder="Enter your new password again..."
                      />
                    </div>
                  </form>
                </div>

                <!-- modal footer -->
                <div class="modal-footer">
                  <!-- button close modal -->
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    data-dismiss="modal"
                  >
                    Close
                  </button>
                  <!-- button confirm create -->
                  <button @click="changePassword" type="button" class="btn btn-primary">
                    Save changes
                  </button>
                </div>
              </div>
            </div>
          </div>

          <div class="singer fs-18 fw-l">{{ userObj.age }} 岁</div>
          <div class="date fs-18 fw-l">{{ userObj.address }}</div>
          <div class="intro fs-18 fw-l">
            {{ userObj.description }}
          </div>
          <div class="album-perf">
            <div class="views fl">
              <p class="fs-18 style-white fl">关注</p>
              <p class="fs-24 style-green style-impact fl number">
                {{ userObj.follow_count }}
              </p>
            </div>
            <div class="likes fl">
              <p class="fs-18 style-white fl">粉丝</p>
              <p class="fs-24 style-green style-impact fl number">
                {{ userObj.fan_count }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "personalIntro",
  props: {
    userObj: Object,
    follows: Array,
    fans: Array,
  },
  methods: {
    modify() {
      const username = document.getElementById("username").value;
      const age = document.getElementById("age").value;
      const gender = document.getElementById("gender").options[
        document.getElementById("gender").value
      ].text;
      const address = document.getElementById("address").value;
      const description = this.$trim(document.getElementById("description").value);
      const avatar = document.getElementById("avatar").files[0];

      this.$poster
        .changeProfile(username, age, gender, address, description, avatar)
        .then((res) => {
          // console.log(res);

          const form = document.getElementById("modifyForm");

          if (res.code == 200) {
            const msg = res.message;

            const div = document.createElement("div");
            div.className = "alert alert-success";
            div.textContent = msg;

            form.insertBefore(div, form.firstElementChild);

            this.$fetcher.account().then((res) => {
              localStorage.setItem("account", JSON.stringify(res));

              setTimeout(() => {
                form.querySelector(".alert").remove();

                this.$reload();
              }, 2000);
            });
          } else {
            const msg = res.message;

            const div = document.createElement("div");
            div.className = "alert alert-danger";
            div.textContent = msg;

            form.insertBefore(div, form.firstElementChild);

            setTimeout(() => {
              form.querySelector(".alert").remove();
            }, 2000);
          }
        });
    },

    changePassword() {
      const oldPassword = document.getElementById("old").value;
      const newPassword = document.getElementById("new").value;
      const confirmPassword = document.getElementById("again").value;

      this.$poster
        .changePassword(oldPassword, newPassword, confirmPassword)
        .then((res) => {
          // console.log(res);

          const form = document.getElementById("passwordForm");

          if (res.code == 200) {
            const msg = res.message;

            const div = document.createElement("div");
            div.className = "alert alert-success";
            div.textContent = msg;

            form.insertBefore(div, form.firstElementChild);

            setTimeout(() => {
              form.querySelector(".alert").remove();

              this.$fetcher.logout().then(() => {
                localStorage.setItem("isLoggedIn", false);
                localStorage.removeItem("account");

                this.$router.push("/login");
                this.$reload();
              });
            }, 2000);
          } else {
            const msg = res.message;

            const div = document.createElement("div");
            div.className = "alert alert-danger";
            div.textContent = msg;

            form.insertBefore(div, form.firstElementChild);

            setTimeout(() => {
              form.querySelector(".alert").remove();
            }, 2000);
          }
        });
    },
  },
  created() {
    // console.log(this.userObj.follows);
    // this.follows = users.follows;
    // this.fans = userObj.fans;
  },
};
</script>

<style scoped>
.album-intro {
  background-color: #21262c !important ;
}
.intro-container {
  width: 1314px;
  margin: 0 auto;
  height: 423px;
  padding-top: 72px;
}
.ablum-info .cover {
  width: 232px;
  height: 315px;
  margin-left: 61px;
  margin-right: 77px;
  background: #659274;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.6);
}
.ablum-info .cover img {
  width: 220px;
  height: 300px;
  margin: 6px 6px;
  border-radius: 10px;
}
.ablum-info .name {
  margin-right: 13px;
  margin-bottom: 6px;
}
.ablum-info .likeAlbum img {
  width: 28px;
  height: 24px;
  transform: translateY(-24px);
  filter: drop-shadow(#659274 0 24px);
}
.ablum-info .info {
  width: 752px;
  height: 163px;
  padding-top: 46px;
}
.ablum-info .info .singer {
  margin: 50px 0px 5px;
}
.ablum-info .info .date {
  margin-bottom: 16px;
}
.ablum-info .info .album-perf {
  margin-top: 26px;
}
.ablum-info .info .album-perf > div {
  margin-right: 26px;
}
.views,
.likes,
.comments :first-child {
  line-height: 36px;
}
.number {
  margin-left: 10px;
}
label {
  color: #000;
}
</style>
