<template>
  <div class="container">

    <!-- song info -->
    <div class="card card-body mt-3">
      <!-- top -->
      <div class="row">

        <!-- left side -->
        <div class="col-md-4">

          <!-- song picture -->
          <img :src="this.$fetcher.mediaUrl+'/'+song.song_picture" alt="" class="img-fluid rounded mb-3">
          <audio @timeupdate="update($event)" :src="this.$fetcher.mediaUrl+'/'+song.song_url" autoplay muted></audio>

          <!-- control panel -->
          <div class="control">

            <!-- progress bar -->
            <div class="progress mb-2" @click="set($event)">
              <div class="progress-bar progress-bar-striped bg-success" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100"></div>
            </div>

            <div class="volume">
              <input @input="volume($event)" id="slider" type="range" min="0" max="100" value="50">
            </div>

            <!-- button prev -->
            <button @click="prev" id="prev" class="btn btn-outline-secondary btn-sm rounded">
              <i class="bi bi-caret-left-fill"></i>
            </button>

            <!-- button play -->
            <button @click="toggle" id="play" class="btn btn-outline-secondary btn-sm rounded ml-2 mr-2 action-btn action-btn-big">
              <i class="bi bi-play-fill"></i>
            </button>

            <!-- button next -->
            <button @click="next" id="next" class="btn btn-outline-secondary btn-sm rounded">
              <i class="bi bi-caret-right-fill"></i>
            </button>

            <!-- button loop -->
            <button @click="loop" id="loop" class="btn btn-outline-secondary btn-sm rounded ml-2 mr-2">
              <i class="bi bi-arrow-clockwise"></i>
            </button>

            <!-- button like -->
            <button @click="fav" id="like" class="btn btn-outline-secondary btn-sm rounded" :class="{ liked: song.is_like }" v-if="id">
              <i class="bi" :class="{ 'bi-heart-fill': song.is_like, 'bi-heart': !song.is_like }"></i>
            </button>
          </div>
        </div>

        <!-- right side -->
        <div class="col-md-8">

          <p class="lead">{{song.song_name}}</p>

          <small class="text-muted">{{song.album_name}} </small>
          <small class="text-muted">/ {{song.singer}} </small>

          <!-- dropdown menu -->
          <div class="dropdown mt-3" v-if="id">

            <!-- button add -->
            <button id="add" class="btn btn-outline-secondary btn-sm rounded dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <i class="bi bi-plus-circle"></i>
            </button>

            <!-- add to playlist menu -->
            <div class="dropdown-menu" aria-labelledby="add">

              <!-- playlists -->
              <button @click="add($event)" :class="{ 'text-success': playlist.hasCurrentSong, isInList: playlist.hasCurrentSong }" class="btn dropdown-item" v-for="playlist in playlists" :key="playlist.id" :data-id="playlist.id">{{playlist.list_name}}</button>

              <!-- button create playlist -->
              <button id="createBtn" type="button" class="btn dropdown-item mt-3" data-toggle="modal" data-target="#create">New list</button>
            </div>

            <!-- modal -->
            <div id="create" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="modalTitle" aria-hidden="true">

              <!-- modal dialog -->
              <div class="modal-dialog modal-dialog-centered" role="document">

                <!-- modal content -->
                <div class="modal-content">

                  <!-- modal header -->
                  <div class="modal-header">
                    <h5 id="modalTitle" class="modal-title">Create new playlist</h5>

                    <!-- button close modal -->
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true">&times;</span>
                    </button>
                  </div>

                  <!-- modal body -->
                  <div class="modal-body">
                    <form>

                      <!-- list name input -->
                      <div class="form-group">
                        <label for="name">List name</label>
                        <input id="name" class="form-control" type="text" placeholder="Enter list name here...">
                      </div>

                      <!-- list description textarea -->
                      <div class="form-group">
                        <label for="description">List description</label>
                        <textarea id="description" class="form-control" rows="3" placeholder="Enter list description here..."></textarea>
                      </div>

                      <!-- list picture input -->
                      <div class="form-group">
                        <label for="picture">List picture</label>
                        <input id="picture" class="form-control" type="file">
                      </div>
                    </form>
                  </div>

                  <!-- modal footer -->
                  <div class="modal-footer">

                    <!-- button close modal -->
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Close</button>
                    <!-- button confirm create -->
                    <button @click="create" type="button" class="btn btn-primary">Save changes</button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- button lyrics -->
          <p class="mt-3">
          <button class="btn btn-outline-secondary rounded" type="button" data-toggle="collapse" data-target="#lyrics" aria-expanded="false" aria-controls="lyrics">LYRICS</button>
          </p>

          <!-- lyrics -->
          <div id="lyrics" class="collapse">
            <div class="card card-body">
              <p class="font-weight-light font-italic" v-for="(line, index) in lyrics" :key=index>{{line}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- album songs -->
    <div class="card card-body mt-3">
      <h4>Songs in album</h4>
      <ul class="list-group">
        <li class="list-group-item d-flex align-items-center justify-content-between" v-for="song in album" :key=song.id>
          {{song.song_name}}
          <router-link :to="'/play?id='+song.id" target="_self" class="btn btn-sm btn-outline-secondary">PLAY</router-link>
        </li>
      </ul>
    </div>

    <!-- comments -->
    <div id="comments" class="card card-body mt-3">
      <h4>Comments</h4>

      <ul class="list-group">

        <!-- for loop comments -->
        <li class="list-group-item" v-for="comment in comments" :key=comment.id>

          <div class="row d-flex justify-content-center">

            <!-- avatar col -->
            <div class="col-md-1">
              <img class="img-fluid rounded" :src="$fetcher.mediaUrl+'/'+comment.sender.avatar" alt="">

              <!-- button uncomment -->
              <button @click="uncomment($event)" v-if="id && id == comment.sender.id" class="btn btn-sm btn-outline-secondary mt-2" :data-id="comment.id">
                <i class="bi bi-x"></i>
              </button>
            </div>

            <!-- comment content -->
            <div class="col-md-10 text-left">
              <div class="mic-info mb-1">
                By: <router-link target="_blank" :to="'/person?id='+comment.sender.id">{{comment.sender.username}}</router-link> on {{comment.comment_time}}
              </div>
              <div class="font-weight-light font-italic">
                <p class="font-weight-light font-italic" v-for="(line, index) in comment.content" :key=index>{{line}}</p>
              </div>
            </div>

            <div class="col-md-1 d-flex align-items-center">

              <!-- button thumbsup -->
              <button @click="thumbs($event)" :data-id="comment.id" class="btn btn-sm btn-outline-secondary" :class="{ liked: comment.is_like }">
                <i class="bi" :class="{ 'bi-hand-thumbs-up': !comment.is_like, 'bi-hand-thumbs-up-fill': comment.is_like }"></i> {{comment.like_count}}
              </button>
            </div>
          </div>
        </li>
      </ul>

      <div class="d-flex justify-content-center mt-3">

        <!-- pagination list -->
        <ul class="pagination">

          <!-- button previous -->
          <li class="page-item">
            <button @click="prevPage" type="button" class="btn btn-sm btn-outline-secondary page-link" :class="{ disabled: page == 1 }">Previous</button>
          </li>

          <!-- button pages -->
          <li class="page-item" v-for="index in pages" :key="index">
            <button @click="jumpPage(index)" type="button" class="btn btn-sm btn-outline-secondary page-link" :class="{ active: index == page }">{{index}}</button>
          </li>

          <!-- button next -->
          <li class="page-item">
            <button @click="nextPage" type="button" class="btn btn-sm btn-outline-secondary page-link" :class="{ disabled: page == pages }">Next</button>
          </li>

        </ul>
      </div>

    </div>

    <!-- my comments -->
    <div class="card card-body mt-3 mb-3">
      <div class="row d-flex justify-content-center">

        <!-- comment form -->
        <form id="commentForm" class="w-75 text-left">

          <!-- comment box -->
          <div class="form-group">
            <textarea id="commentBox" class="form-control" rows="5" placeholder="Leave your comments here with no more than 200 words"></textarea>
          </div>

          <!-- button comment -->
          <div class="form-group">
            <button @click="comment($event)" id="submitBtn" class="btn btn-outline-secondary" type="submit">Submit comment</button>
          </div>

        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Play',

  data() {
    return {
      song: {},

      lyrics: [],

      album: [],

      comments: [],

      playlists: [],

      id: null,

      pages: null,

      page: 1,
    };
  },

  methods: {

    play() {
      const playBtn = document.getElementById('play');

      playBtn.classList.add('play');

      playBtn.querySelector("i.bi").classList.remove("bi-play-fill");
      playBtn.querySelector("i.bi").classList.add("bi-pause-fill");

      document.querySelector('audio').play();
    },

    pause() {
      const playBtn = document.getElementById('play');

      playBtn.classList.remove('play');

      playBtn.querySelector("i.bi").classList.add("bi-play-fill");
      playBtn.querySelector("i.bi").classList.remove("bi-pause-fill");

      document.querySelector('audio').pause();
    },

    toggle() {
      const isPlaying = document.getElementById('play').classList.contains('play');

      if (!isPlaying) this.play();
      else this.pause();
    },

    update(e) {
      const { duration, currentTime } = e.srcElement;
      const progressPercent = (currentTime / duration) * 100;
      const progressBar = document.querySelector('.progress-bar');

      progressBar.style.width = `${progressPercent}%`;
      progressBar.setAttribute('aria-valuenow', progressPercent);

      const audio = document.querySelector('audio');

      if (currentTime == duration) {

        if (audio.loop == true || (audio.loop == false && this.album.length == 1)) {

          audio.currentTime = 0;
          this.play();

        } else {

          this.next();

        }
      }
    },

    set(e) {
      const duration = document.querySelector('audio').duration;

      const width = document.querySelector('.progress').offsetWidth;

      const offset = e.offsetX;

      document.querySelector('audio').currentTime = (offset / width) * duration;
    },

    volume(e) {
      const volume = e.target.value / 100

      document.querySelector('audio').volume = volume;

      localStorage.setItem('volume', volume);
    },

    prev() {
      this.album.forEach((song, index) => {
        if (song.id == this.song.id) {
          this.$router.push(`/play?id=${this.album[(index+this.album.length-1)%(this.album.length)].id}`);
        }
      });
    },

    next() {
      this.album.forEach((song, index) => {
        if (song.id == this.song.id) {
          this.$router.push(`/play?id=${this.album[(index+1)%(this.album.length)].id}`);
        }
      });
    },

    like() {
      const likeBtn = document.getElementById('like');

      this.$poster.likeSong(this.song.id, 1)
        .then(res => {
          // console.log(res);

          if (res.code == 200) {

            likeBtn.classList.add('liked');

            likeBtn.querySelector('i.bi').classList.add('bi-heart-fill');
            likeBtn.querySelector('i.bi').classList.remove('bi-heart');

            const msg = res.warning;

            const div = document.createElement('div');
            div.className = 'alert alert-success';
            div.textContent = msg;

            document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('.col-md-8').querySelector('.alert').remove();
            }, 2000);
          } else {
            const msg = res.errMsg;

            const div = document.createElement('div');
            div.className = 'alert alert-danger';
            div.textContent = msg;

            document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('.col-md-8').querySelector('.alert').remove();
            }, 2000);
          }
        });
    },

    hate() {
      const likeBtn = document.getElementById('like');

      this.$poster.likeSong(this.song.id, 2)
        .then(res => {
          // console.log(res);

          if (res.code == 200) {

            likeBtn.classList.remove('liked');

            likeBtn.querySelector('i.bi').classList.add('bi-heart');
            likeBtn.querySelector('i.bi').classList.remove('bi-heart-fill');

            const msg = res.warning;

            const div = document.createElement('div');
            div.className = 'alert alert-success';
            div.textContent = msg;

            document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('.col-md-8').querySelector('.alert').remove();
            }, 2000);
          } else {
            const msg = res.errMsg;

            const div = document.createElement('div');
            div.className = 'alert alert-danger';
            div.textContent = msg;

            document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('.col-md-8').querySelector('.alert').remove();
            }, 2000);
          }
        })
    },

    fav() {
      const isLiked = document.getElementById('like').classList.contains('liked');

      if (!isLiked) this.like();
      else this.hate();
    },

    loop() {

      const audio = document.querySelector('audio');
      const loop = document.getElementById('loop');

      if (audio.loop == true) {

        loop.querySelector('i.bi').classList.add('bi-arrow-repeat');
        loop.querySelector('i.bi').classList.remove('bi-arrow-clockwise');

        audio.loop = !audio.loop;

        localStorage.setItem('loop', audio.loop);

      } else {

        loop.querySelector('i.bi').classList.add('bi-arrow-clockwise');
        loop.querySelector('i.bi').classList.remove('bi-arrow-repeat');

        audio.loop = !audio.loop;

        localStorage.setItem('loop', audio.loop);

      }

      // console.log(audio.loop);
    },

    create() {

      const name = document.getElementById('name').value;
      const description = document.getElementById('description').value;

      const picture = document.getElementById('picture').files[0];

      this.$poster.createList(name, description, picture)
        .then(res => {
          if (res.code == 200) {

            const msg = res.message;

            const div = document.createElement('div');
            div.className = 'alert alert-success';
            div.textContent = msg;

            document.querySelector('.modal-body').parentElement.insertBefore(div, document.querySelector('.modal-body'));

            setTimeout(() => {

              document.querySelector('.modal-content').querySelector('.alert').remove();

              this.$reload();
            }, 2000);

          } else {

            const msg = res.message;

            const div = document.createElement('div');
            div.className = 'alert alert-danger';
            div.textContent = msg;

            document.querySelector('.modal-body').parentElement.insertBefore(div, document.querySelector('.modal-body'));

            setTimeout(() => {
              document.querySelector('.modal-content').querySelector('.alert').remove();
            }, 2000);
          }
        });
    },

    add(e) {

      const button = e.target;

      if (!button.classList.contains('isInList')) {

        this.$poster.addToList(this.song.id, parseInt(button.getAttribute('data-id')), 1)
          .then(res => {
            console.log(res);

            if (res.code == 200) {
              button.classList.add('isInList');
              button.classList.add('text-success');

              const msg = res.message;

              const div = document.createElement('div');
              div.className = 'alert alert-success';
              div.textContent = msg;

              document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

              setTimeout(() => {
                document.querySelector('.col-md-8').querySelector('.alert').remove();
              }, 2000);
            } else {
              const msg = res.errMsg;

              const div = document.createElement('div');
              div.className = 'alert alert-danger';
              div.textContent = msg;

              document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

              setTimeout(() => {
                document.querySelector('.col-md-8').querySelector('.alert').remove();
              }, 2000);
            }
          });
      } else {

        this.$poster.addToList(this.song.id, parseInt(button.getAttribute('data-id')), 2)
          .then(res => {
            console.log(res);

            if (res.code == 200) {
              button.classList.remove('isInList');
              button.classList.remove('text-success');

              const msg = res.message;

              const div = document.createElement('div');
              div.className = 'alert alert-success';
              div.textContent = msg;

              document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

              setTimeout(() => {
                document.querySelector('.col-md-8').querySelector('.alert').remove();
              }, 2000);
            } else {
              const msg = res.errMsg;

              const div = document.createElement('div');
              div.className = 'alert alert-danger';
              div.textContent = msg;

              document.querySelector('.col-md-8').insertBefore(div, document.querySelector('.col-md-8').firstElementChild.nextElementSibling);

              setTimeout(() => {
                document.querySelector('.col-md-8').querySelector('.alert').remove();
              }, 2000);
            }
          });
      }
    },

    thumbsup(e) {
      const btn = e.target;

      this.$poster.likeComment(parseInt(btn.getAttribute('data-id')), 1)
        .then(res => {
          // console.log(res);

          if (res.code == 200) {

            btn.classList.add('liked');

            btn.querySelector('i.bi').classList.add('bi-hand-thumbs-up-fill');
            btn.querySelector('i.bi').classList.remove('bi-hand-thumbs-up');

            this.comments.forEach((comment) => {
              if (comment.id == parseInt(btn.getAttribute('data-id'))) { comment.like_count = comment.like_count + 1; }
            });

            const msg = res.message;

            const div = document.createElement('div');
            div.className = 'alert alert-success';
            div.textContent = msg;

            document.querySelector('#comments').insertBefore(div, document.querySelector('#comments').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('#comments').querySelector('.alert').remove();
            }, 2000);
          } else {
            const msg = res.errMsg;

            const div = document.createElement('div');
            div.className = 'alert alert-danger';
            div.textContent = msg;

            document.querySelector('#comments').insertBefore(div, document.querySelector('#comments').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('#comments').querySelector('.alert').remove();
            }, 2000);
          }
        });
    },

    thumbsdown(e) {
      const btn = e.target;

      this.$poster.likeComment(parseInt(btn.getAttribute('data-id')), 2)
        .then(res => {
          // console.log(res);

          if (res.code == 200) {

            btn.classList.remove('liked');

            btn.querySelector('i.bi').classList.add('bi-hand-thumbs-up');
            btn.querySelector('i.bi').classList.remove('bi-hand-thumbs-up-fill');

            this.comments.forEach((comment) => {
              if (comment.id == parseInt(btn.getAttribute('data-id'))) { comment.like_count = comment.like_count - 1; }
            })

            const msg = res.message;

            const div = document.createElement('div');
            div.className = 'alert alert-success';
            div.textContent = msg;

            document.querySelector('#comments').insertBefore(div, document.querySelector('#comments').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('#comments').querySelector('.alert').remove();
            }, 2000);
          } else {
            const msg = res.errMsg;

            const div = document.createElement('div');
            div.className = 'alert alert-danger';
            div.textContent = msg;

            document.querySelector('#comments').insertBefore(div, document.querySelector('#comments').firstElementChild.nextElementSibling);

            setTimeout(() => {
              document.querySelector('#comments').querySelector('.alert').remove();
            }, 2000);
          }
        })
    },

    thumbs(e) {
      const isLiked = e.target.classList.contains('liked');

      if (!isLiked) this.thumbsup(e);
      else this.thumbsdown(e);
    },

    comment(e) {
      e.preventDefault();

      const form = document.getElementById('commentForm');

      const content = this.$trim(document.getElementById('commentBox').value);

      if (content.match(/<script>.*<\/script>/) !== null) {

        const msg = 'Dangerous message';

        const div = document.createElement('div');
        div.className = 'alert alert-danger';
        div.textContent = msg;

        form.insertBefore(div, form.firstElementChild);

        setTimeout(() => {
          form.querySelector('.alert').remove();
        }, 2000);

      } else {

        this.$poster.commentSong(this.song.id, content)
          .then(res => {
            // console.log(res);

            if (res.code == 200) {

              const msg = res.message;

              const div = document.createElement('div');
              div.className = 'alert alert-success';
              div.textContent = msg;

              form.insertBefore(div, form.firstElementChild);

              setTimeout(() => {

                form.querySelector('.alert').remove();

                this.$reload();
              }, 2000);

            } else {

              const msg = res.errMsg;

              const div = document.createElement('div');
              div.className = 'alert alert-danger';
              div.textContent = msg;

              form.insertBefore(div, form.firstElementChild);

              setTimeout(() => {
                form.querySelector('.alert').remove();
              }, 2000);
            }
          });
      }
    },

    uncomment(e) {

      const btn = e.target;

      if (confirm('Are you sure to delete this comment') == true) {
        this.$poster.uncommentSong(parseInt(btn.getAttribute('data-id')))
          .then((res) => {
            // console.log(res);

            if (res.code == 200) {

              const msg = res.message;

              const div = document.createElement('div');
              div.className = 'alert alert-success';
              div.textContent = msg;

              document.querySelector('#comments').insertBefore(div, document.querySelector('#comments').firstElementChild.nextElementSibling);

              setTimeout(() => {
                document.querySelector('#comments').querySelector('.alert').remove();

                this.$reload();
              }, 2000);

            } else {

              const msg = res.errMsg;

              const div = document.createElement('div');
              div.className = 'alert alert-danger';
              div.textContent = msg;

              document.querySelector('#comments').insertBefore(div, document.querySelector('#comments').firstElementChild.nextElementSibling);

              setTimeout(() => {
                document.querySelector('#comments').querySelector('.alert').remove();
              }, 2000);
            }
          });
      }
    },

    prevPage() {

      const pageNum = this.page == 1 ? this.pages : this.page - 1;

      this.$fetcher.comments(this.song.id, pageNum)

        .then(res => {

          this.comments = res.comments;

          this.comments.forEach((comment) => {

            comment.comment_time = new Date(comment.comment_time.replace(/-/g, '/').replace('T', ' '));

            function join(date, type, separator) {

              function format(t) {
                let dft = new Intl.DateTimeFormat('en', t);
                return dft.format(date);
              }

              return type.map(format).join(separator);
            }

            let type = [{day: 'numeric'}, {month: 'short'}, {year: 'numeric'}];

            comment.comment_time = join(comment.comment_time, type, ' ');

            comment.content = comment.content.split('\n');
          });

          this.page = pageNum;
        });
    },

    nextPage() {

      const pageNum = this.page == this.pages ? 1 : this.page + 1;

      this.$fetcher.comments(this.song.id, pageNum)

        .then(res => {

          this.comments = res.comments;

          this.comments.forEach((comment) => {

            comment.comment_time = new Date(comment.comment_time.replace(/-/g, '/').replace('T', ' '));

            function join(date, type, separator) {

              function format(t) {
                let dft = new Intl.DateTimeFormat('en', t);
                return dft.format(date);
              }

              return type.map(format).join(separator);
            }

            let type = [{day: 'numeric'}, {month: 'short'}, {year: 'numeric'}];

            comment.comment_time = join(comment.comment_time, type, ' ');

            comment.content = comment.content.split('\n');
          });

          this.page = pageNum;
        });
    },

    jumpPage(pageNum) {

      this.$fetcher.comments(this.song.id, pageNum)

        .then(res => {

          this.comments = res.comments;

          this.comments.forEach((comment) => {

            comment.comment_time = new Date(comment.comment_time.replace(/-/g, '/').replace('T', ' '));

            function join(date, type, separator) {

              function format(t) {
                let dft = new Intl.DateTimeFormat('en', t);
                return dft.format(date);
              }

              return type.map(format).join(separator);
            }

            let type = [{day: 'numeric'}, {month: 'short'}, {year: 'numeric'}];

            comment.comment_time = join(comment.comment_time, type, ' ');

            comment.content = comment.content.split('\n');
          });

          this.page = pageNum;
        });
    },

  },

  watch: {

    // Reload whenever id changes
    "$route.query.id"() { this.$reload(); }
  },

  created() {

    // Fetch song data
    this.$fetcher.song(this.$route.query.id)
      .then(song => {
        // console.log(song);

        this.song = song;

        // Fetch lyrics
        fetch(`/media/${song.lyric}`).then(res => {

            if (res.status == 403) {
              return 'No lyrics for this song';
            } else {
              return res.text();
            }

          }).then(res => {
            this.lyrics = res.split('\n');
          });

        // Fetch album
        this.$fetcher.album(song.album)
          .then(album => {
            // console.log(album);

            this.album = album.songs;
          });
      });

    // Fetch comments
    this.$fetcher.comments(this.$route.query.id, this.page)
      .then(res => {
        // console.log(res);

        this.comments = res.comments;

        this.pages = res.num_pages;

        this.comments.forEach((comment) => {

          comment.comment_time = new Date(comment.comment_time.replace(/-/g, '/').replace('T', ' '));

          function join(date, type, separator) {

            function format(t) {
              let dft = new Intl.DateTimeFormat('en', t);
              return dft.format(date);
            }

            return type.map(format).join(separator);
          }

          let type = [{day: 'numeric'}, {month: 'short'}, {year: 'numeric'}];

          comment.comment_time = join(comment.comment_time, type, ' ');

          comment.content = comment.content.split('\n');
        })
      });

    // Fetch user created lists if logged in
    if (JSON.parse(localStorage.getItem('isLoggedIn')) == true) {

      this.$fetcher.playlists()
        .then(playlists => {
          // console.log(playlists);

          this.playlists = playlists;

          this.playlists.forEach((playlist) => {

            playlist.song.forEach((song) => {
              if (song.song_id == this.song.id) { playlist.hasCurrentSong = true; }
            });
          });
        });

      // Fetch user id for uncommenting
      this.$fetcher.id().then(id => { this.id = id; });

    } else {

      this.playlists = undefined;

    }

    // Fetch user id for uncommenting
    if (JSON.parse(localStorage.getItem('isLoggedIn')) == true) {
      this.$fetcher.id().then(id => { this.id = id; });
    }
  },

  mounted() {

    const audio = document.querySelector('audio');

    if (JSON.parse(localStorage.getItem('loop')) == true) {

      audio.loop = false;
      this.loop()

    } else {

      audio.loop = true;
      this.loop();

    }

    const volume = JSON.parse(localStorage.getItem('volume'));

    audio.volume = volume;

    const slider = document.getElementById('slider');

    slider.value = slider.max * volume;

    this.play();

    setTimeout(() => {

      audio.pause();
      audio.load();

    }, 10);

    audio.muted = false;

  }
}
</script>

