<template>
  <div class="container d-flex justify-content-center">

    <!-- signin box -->
    <div id="signinBox" class="card card-body mt-3 col-md-6">
      <h3 class="card-title">Sign in</h3>

      <!-- submit form -->
      <form>

        <!-- button change -->
        <button @click="change" type="button" class="btn btn-sm btn-outline-secondary">Sign up</button>

        <!-- username input -->
        <div class="form-group text-left">
          <label for="username_1">Username</label>
          <input id="username_1" class="form-control" type="text" placeholder="Enter your username here...">
        </div>

        <!-- password input -->
        <div class="form-group text-left">
          <label for="password">Password</label>
          <input id="password" class="form-control" type="password" placeholder="Enter your password here...">
        </div>

        <!-- button submit -->
        <button @click="signin($event)" type="submit" class="btn btn-outline-secondary">Sign in</button>
      </form>
    </div>

    <!-- signup box -->
    <div id="signupBox" class="card card-body mt-3 col-md-6" style="display:none;">
      <h3 class="card-title">Sign up</h3>

      <!-- submit form -->
      <form>

        <!-- button change -->
        <button @click="change" type="button" class="btn btn-sm btn-outline-secondary">Sign in</button>

        <!-- username input -->
        <div class="form-group text-left">
          <label for="username_2">Username</label>
          <input id="username_2" class="form-control" type="text" placeholder="Enter your username here...">
        </div>

        <!-- password1 input -->
        <div class="form-group text-left">
          <label for="password1">Password</label>
          <input id="password1" class="form-control" type="password" placeholder="Enter your password here...">
        </div>

        <!-- password2 input -->
        <div class="form-group text-left">
          <label for="password2">Confirm password</label>
          <input id="password2" class="form-control" type="password" placeholder="Enter your password again...">
        </div>

        <!-- email input -->
        <div class="form-group text-left">
          <label for="email">Email</label>
          <input id="email" class="form-control" type="email" placeholder="Enter your email address here...">
        </div>

        <!-- code input -->
        <div class="form-group text-left">
          <label for="code">Verification code</label>

          <!-- verify group -->
          <div class="d-flex justify-content-between">
            <input id="code" class="form-control" type="text" placeholder="Enter verification code here...">
            <button @click="send" type="button" class="btn btn-outline-secondary">Get verification code</button>
          </div>
        </div>

        <!-- button submit -->
        <button @click="verify($event)" type="submit" class="btn btn-outline-secondary">Sign up</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  methods: {
    showMsg(msg, className, success) {

      const div = document.createElement('div');
      div.className = 'container '+className;
      div.textContent = msg;

      document.querySelector('.container').parentElement.insertBefore(div, document.querySelector('.container'));

      setTimeout(() => {

        document.querySelector('.alert').remove();
        if (success) { this.$router.push('/'); }

      }, 2000);
    },

    change() {

      if (document.getElementById('signinBox').style.display == 'none') {

        document.getElementById('signinBox').style.display = 'block';
        document.getElementById('signupBox').style.display = 'none';

        this.$router.push('/login');
      } else {

        document.getElementById('signupBox').style.display = 'block';
        document.getElementById('signinBox').style.display = 'none';

        this.$router.push('/register');
      }
    },

    signin(e) {

      e.preventDefault();

      // Get values
      const username = document.querySelector('#signinBox #username_1').value;
      const password = document.querySelector('#signinBox #password').value;

      this.$poster.signin(username, password)
        .then(res => {
          // console.log(res);

          if (res.code == 200) {

            const msg = res.message;

            localStorage.setItem('isLoggedIn', true);

            this.showMsg(msg, 'alert alert-success', true);

          } else {
            const msg = res.errMsg;

            this.showMsg(msg, 'alert alert-danger', false);
          }
        });
    },

    send() {

      // Get values
      const username = document.querySelector('#signupBox #username_2').value;
      const password1 = document.querySelector('#signupBox #password1').value;
      const password2 = document.querySelector('#signupBox #password2').value;
      const email = document.querySelector('#signupBox #email').value;

      this.$poster.send(username, password1, password2, email)
        .then(res => {
          console.log(res);

          if (res.code == 200) {

            const msg = res.warning;

            this.showMsg(msg, 'alert alert-success', false);

          } else {
            const msg = res.errMsg;

            this.showMsg(msg, 'alert alert-danger', false);
          }
        });
    },

    verify(e) {

      e.preventDefault();

      const code = document.querySelector('#signupBox #code').value;

      this.$poster.verify(code)
        .then(res => {
          console.log(res);

          if (res.code == 200) {

            const msg = res.warning;

            this.showMsg(msg, 'alert alert-success', false);

            this.$router.push('/login');

            this.$reload();

          } else {
            const msg = res.errMsg;

            this.showMsg(msg, 'alert alert-danger', false);
          }
        });
    },
  },
}
</script>