<script>
import Header from "../components/Header";
import HomeTitle from "../components/HomePage/HomeTitle";
import albumIntro from "../components/album-info/album-intro.vue";
import albumFooter from "../components/albumFooter.vue";

import songsImg from "../../images/img/列表.png";

import songsCommentImg from "../../images/img/评 论.png";

import commentImg from "../../images/img/评论.png";

import axios from "axios";
export default {
  name: "album",
  components: {
    Header,
    HomeTitle,
    albumIntro,
    albumFooter,
  },
  data() {
    return {
      commentObj: {
        title: "评论",
        titleimg: commentImg,
        color: "filter: invert(100%)",
      },
      songsObj: {
        title: "歌曲列表",
        titleimg: songsImg,
        color: "filter: invert(100%)",
      },
      songsCommentObj: {
        title: "歌曲评论",
        titleimg: songsCommentImg,
        color: "filter: invert(100%)",
      },
      album: {},
      comment: [],
      commentContent: "",
      // likeComment: false,
      likeComment: [
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
      ],
      likeList: [],
      likeCommentObj: {},

      likeSong: [
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
      ],
      likeSongObj: {},
    };
  },
  methods: {
    // 点赞评论
    LikeCommentOfAlbum(index, id) {
      //封装post的对象
      this.likeCommentObj.comment_id = id;
      let list = this.likeList;
      this.likeCommentObj.type = list.indexOf(id) == -1 ? 1 : 2;

      // console.log(this.likeCommentObj);
      axios.post("/itmusic/comment_like/album", this.likeCommentObj).then((res) => {
        if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        } else {
          this.likeComment[index] = !this.likeComment[index];
          let list = this.likeList;
          if (list.indexOf(id) == -1) {
            //如果没在likeList中找到这个 id
            prompt("点赞成功");
            this.likeList.push(id);
            // console.log(this.likeList);
          } else {
            //如果没在likeList中找到了 id
            for (var i in this.likeList) {
              if (this.likeList == id) {
                this.likeList.splice(i, 1); //删除这个 id
              }
            }
            prompt("取消点赞成功");
            // console.log(this.likeList);
          }
        }
      });
    },
    likeSongEvent(index, id) {
      this.likeSongObj.song_id = id;
      this.likeSongObj.type =  this.likeSong[index] ? 2 : 1;
      axios.post("/itmusic/collect_song", this.likeSongObj).then((res) => {
          if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        } else {
          this.likeSong[index] = !this.likeSong[index];
          if (!this.likeSong[index]) {
            prompt("取消收藏成功~");
          } else {
            prompt("收藏成功~");
          }
        }
        }
      );
    },
    // 发送评论
    SendComment() {
      axios.post("/itmusic/comment/album", this.commentContent).then((res) => {
        // console.log(res.code);
        console.log(res.data);
        if (res.data.code == "404 NOT FOUND") {
          prompt("请先登录！");
        }
      });
    },
  },
  created() {
    
    axios.get(`/itmusic/album/detail?id=${this.$route.query.id}`).then((res) => {
      this.album = res.data.album;
      this.album.upload_time = this.album.upload_time.slice(0, 10);
      console.log("专辑页面的数据",this.album);
      for (i=0; i <= this.album.songs.length - 1; i++) {
        this.likeSong[i] = this.album.songs[i].is_like;
        console.log("后端的喜欢逻辑数组",this.album.songs[i].is_like);
        console.log("本地的喜欢逻辑数组",this.likeSong[i]);
      }
    });

    axios.get(`/itmusic/comment/album?id=${this.$route.query.id}`).then((res) => {
      this.comment = res.data.comments;
      // console.log(this.comment);
    });

    //初始化渲染：根据每条评论的 is_like 生成一个 likeList。
    for (var i in this.comment) {
      if (i.is_like == true) {
        this.likeList.push(i.id);
      }
    }
    // console.log(this.album);
  },
};
</script>
