<template>
  <!-- main content -->
  <div class="container">

    <!-- search input -->
    <div class="card card-body mt-3 search">
      <h2>Searching...</h2>

      <p class="lead">Enter keywords and get detail</p>

      <form class="d-flex justify-content-between">

        <!-- input element -->
        <input type="text" id="searchInput" placeholder="Song / Album / Playlist / User name" class="form-control mr-3">

        <!-- submit button -->
        <button type="submit" class="btn btn-primary" @click="search($event)">SEARCH</button>

      </form>
    </div>

    <!-- search results -->
    <div class="card card-body mt-3 results">

      <!-- tab pane -->
      <ul id="tab" class="nav nav-tabs" role="tablist">

        <!-- song tab -->
        <li class="nav-item">
          <a id="song-tab" class="nav-link active" data-toggle="tab" role="tab" aria-controls="song" aria-selected="true" href="#song">Songs</a>
        </li>

        <!-- album tab -->
        <li class="nav-item">
          <a id="album-tab" class="nav-link" data-toggle="tab" role="tab" aria-controls="album" aria-selected="false" href="#album">Albums</a>
        </li>

        <!-- playlist tab -->
        <li class="nav-item">
          <a id="playlist-tab" class="nav-link" data-toggle="tab" role="tab" aria-controls="playlist" aria-selected="false" href="#playlist">Playlists</a>
        </li>

        <!-- user tab -->
        <li class="nav-item">
          <a id="user-tab" class="nav-link" data-toggle="tab" role="tab" aria-controls="user" aria-selected="false" href="#user">Users</a>
        </li>
      </ul>

      <!-- tab content -->
      <div class="tab-content">

        <!-- songs -->
        <div id="song" class="song tab-pane fade show active" role="tabpanel" aria-labelledby="song-tab">
          <h3 class="mt-3">Songs</h3>
          <ul class="list-group">
            <li class="list-group-item d-flex align-items-center justify-content-between" v-for="song in result.song" :key="song.id">
              {{song.song_name}} / {{song.album_name}} / {{song.singer}}
              <router-link :to="'/play?id='+song.id" target="_blank" class="btn btn-primary">PLAY</router-link>
            </li>
          </ul>
        </div>

        <!-- albums -->
        <div id="album" class="album tab-pane fade" role="tabpanel" aria-labelledby="album-tab">
          <h3 class="mt-3">Albums</h3>
          <ul class="list-group">
            <li class="list-group-item d-flex align-items-center justify-content-between" v-for="album in result.album" :key="album.id">
              {{album.album_name}} / {{album.singer}}
              <router-link :to="'/album?id='+album.id" target="_blank" class="btn btn-primary">VIEW DETAILS</router-link>
            </li>
          </ul>
        </div>

        <!-- playlists -->
        <div id="playlist" class="playlist tab-pane fade" role="tabpanel" aria-labelledby="playlist-tab">
          <h3 class="mt-3">Playlists</h3>
          <ul class="list-group">
            <li class="list-group-item d-flex align-items-center justify-content-between" v-for="playlist in result.playlist" :key="playlist.id">
              {{playlist.list_name}}
              <router-link :to="'/playlist?id='+playlist.id" target="_blank" class="btn btn-primary">VIEW DETAILS</router-link>
            </li>
          </ul>
        </div>

        <!-- users -->
        <div id="user" class="user tab-pane fade" role="tabpanel" aria-labelledby="user-tab">
          <h3 class="mt-3">Users</h3>
          <ul class="list-group">
            <li class="list-group-item d-flex align-items-center justify-content-between" v-for="user in result.user" :key="user.id">
              {{user.username}}
              <router-link :to="'/user?id='+user.id" target="_blank" class="btn btn-primary">VIEW DETAILS</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {

  name: 'Search',

  data() {
    return {

      result: {

        song: [],

        album: [],

        playlist: [],

        user: [],
      },
    };
  },

  methods: {

    search(e) {

      e.preventDefault();

      const keywords = document.getElementById('searchInput').value;

      this.$router.push(`/search?keywords=${keywords}`);

      this.$reload();
    }

  },

  created() {

    this.$fetcher.search(this.$route.query.keywords)
      .then(result => {

        // console.log(result);

        this.result = result;
      });
  },
}
</script>