<template>
  <div class="list-intro">
    <div class="intro-container style-white">
      <div class="ablum-info clearfix">
        <div class="cover fl">
          <img
            :src="'http://47.102.215.61/media/'+list.list_picture"
            alt=""
          />
        </div>
        <div class="info">
          <div class="name fs-24 fw-b fl">{{ list.list_name }} </div>
          <div class="likelist" @click="collectPlaylist"><img src="../../../imgx/喜欢.png" alt="" :class="[isCollect ? 'collect' : '']"/></div>
          <!-- <div class="singer fs-18 fw-l">{{ list.creator.username }}</div> -->
          <div class="date fs-18 fw-l">{{ list.upload_time }}</div>
          <div class="intro fs-18 fw-l">
            <p class="fs-18 fw-l">{{ list.description }}</p>
          </div>
          <div class="list-perf">
            <div class="views fl">
              <p class="fs-18 style-white fl">播放</p>
              <p class="fs-24 style-green style-impact fl number">{{list.play_count}}</p>
            </div>
            <div class="likes fl">
              <p class="fs-18 style-white fl">喜欢</p>
              <p class="fs-24 style-green style-impact fl number">{{list.like_count}}</p>
            </div>
            <div class="comments fl">
              <p class="fs-18 style-white fl">评论</p>
              <p class="fs-24 style-green style-impact fl number">{{list.comment_count}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: "listIntro",
  data() {
    return {
      collectPlayListObj: {},
      isCollect: true,
    };
  },
  props: {
    list: Object,
  },
  methods: {
    collectPlaylist() {
      this.collectPlayListObj.list_id = this.list.id;
      this.collectPlayListObj.type = this.list.is_like == true ? 2 : 1;
      // console.log(this.collectPlayListObj);
      axios.post('/itmusic/collect_playlist', this.collectPlayListObj)
      .then(res => {
        console.log(res.data);
        if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        }else {
          this.isCollect = !this.isCollect;
          if (!this.isCollect) {
            prompt("取消收藏成功~");
          } else {
            prompt("收藏成功~");
          }
        }
      })
      // prompt("成功收藏歌单！");
    }
  },
  created() {
    axios.get(`/itmusic/playlist/detail?id=${this.$route.query.id}`).then(res => {
      console.log("喜欢的歌单初始化",res.data);
      (this.isCollect = res.data.list.is_like),
        console.log(
          "2 本地逻辑变量",
          this.isCollect,
          "2 接口逻辑变量",
          res.data.play.is_like
        );
    })
  }

};
</script>
<style scoped>
.collect {
    transform: translateY(-24px);
  filter: drop-shadow(#659274 0 24px);
}
.list-intro {
  width: 100%;
  background-color: cornsilk;
  background: url("../../../images/upload_img/图片/marek-piwnicki-JnXebVlsVrg-unsplash 拷贝.png")
    no-repeat;
  background-size: 100% 100%;
}
.intro-container {
  width: 1314px;
  margin: 0 auto;
  height: 423px;
  padding-top: 72px;
}
.ablum-info .cover {
  width: 346px;
  height: 346px;
  margin-left: 61px;
  margin-right: 77px;
  border-radius: 173px;
  background-color: pink;
}
.ablum-info .cover img {
  width: 346px;
  height: 346px;
  border-radius: 173px;
}
.ablum-info .name {
  margin-right: 13px;
}
.ablum-info .likelist {
  width: 30px;
  overflow: hidden;
  /* height: 24px; */
  margin: 6px 0px 6px;
  cursor: pointer;
}
.ablum-info .likelist img {
  width: 28px;
  height: 24px;

}
.ablum-info .info {
  width: 1175px;
  height: 163px;
  padding-top: 60px;
}
.ablum-info .info .date {
  margin-top: 8px;
}
.ablum-info .info .list-perf {
  margin-top: 15px;
}
.ablum-info .info .list-perf > div {
  margin-right: 26px;
}
.views,
.likes,
.comments :first-child {
  line-height: 36px;
}
.number {
  margin-left: 10px;
}
</style>
