<template>
  <div class="Like-Album">
    <HomeTitle :obj="tileObj" class="title"></HomeTitle>
    <ul>
      <li v-for="(item,index) in currentPageData" :key="index" class="d-flex" :style="{backgroundColor: (index+1)%2!=0 ? '#efefef' : ''}">
                <img :src="'http://47.102.215.61/media/'+item.list_picture" alt="" class="hovered-img">
                <div class="hovered-div"><router-link :to="'/playlist?id=' + item.id" class="song-name">{{item.list_name}}</router-link>
                <div class="writer-name">{{item.singer}}</div>
                <div class="data">{{item.upload_time.slice(0,10)}}</div>
                <div class="introduce">{{item.description}}</div>
                </div>
                <div class="hovered-fonter">
                <div class="attention">关注&nbsp;&nbsp;<span>{{item.like_count}}</span></div>
                <div class="comment">评论&nbsp;&nbsp;<span>{{item.comment_count}}</span></div>
          <div class="icon d-flex">
            <img src="../../../imgx/播放量.png" alt="" class="play" />
            <div class="store-up" @click="CancleCollect(index,item.list_id)">
              <img
                src="../../../imgx/收藏拉宽.png"
                alt=""
                class="collect"
                :class="[is_playList_like[index] ? 'store-upImg' : '']"
              />
            </div>
          </div>
        </div>
      </li>
    </ul>
    <div class="pagingDevice d-flex">
            <button @click="prevPage()" class="changeBtn">
                上一页
            </button>
            <span class="d-flex">第{{currentPage}}页/共{{totalPage}}页</span>
            <button @click="nextPage()" class="changeBtn">
                下一页
            </button>
        </div>
  </div>
</template>
<script>
import axios from 'axios'
import HomeTitle from "../HomePage/HomeTitle";
import img from "../../img/矢量图/我的.png";

export default {
  props: {
    songList: Array,

  },
  data() {
    return {
      totalPage: 1, // 统共页数，默认为1
            currentPage: 1, //当前页数 ，默认为1
            pageSize: 4, // 每页显示数量
            currentPageData: [], //当前页显示内容
      tileObj: {
        title: "我的歌单",
        titleimg: img,
        color:"filter:invert(100%)",
      },
      is_playList_like: [
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
        true,
      ],
      cancleCollectObj: {},
    };
  },
  methods: {
     setCurrentPageData() {
            let begin = (this.currentPage - 1) * this.pageSize;
            let end = this.currentPage * this.pageSize;
            this.currentPageData = this.songList.slice(
                begin,
                end
            );
        },
        //上一页
        prevPage() {
            console.log(this.currentPage);
            if (this.currentPage == 1) return;
         
             this.currentPage--;
             this.setCurrentPageData();
            console.log(this.songList);
        },
        // 下一页
        nextPage() {
            if (this.currentPage == this.totalPage)return ;
             this.currentPage++;
             this.setCurrentPageData();
            console.log(this.songList);
        },
      CancleCollect(index, id) {
      this.is_playList_like[index] = !this.is_playList_like[index];
      this.cancleCollectObj.album_id = id;
      this.cancleCollectObj.type = 2;
      axios.post('/itmusic/collect_playlists', this.cancleCollectObj)
      .then(res => {
        console.log(res.data);
        if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        }
        else {
          prompt("取消收藏成功~");
        }
      })
    }
  },
  components: {
    HomeTitle,
  },
    mounted(){
        // 计算一共有几页
        this.totalPage = Math.ceil(this.songList.length / this.pageSize);
        // 计算得0时设置为1
        this.totalPage = this.totalPage == 0 ? 1 : this.totalPage;
        this.setCurrentPageData();
        // console.log('fuck');
        console.log(this.currentPageData);
        
    },
};
</script>
