    likeSongEvent(index, id) {
      this.likeSongObj.song_id = id;
      this.likeSongObj.type =  this.likeSong[index] ? 2 : 1;
      axios.post("/itmusic/collect_song", this.likeSongObj).then((res) => {
          if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        } else {
          this.likeSong[index] = !this.likeSong[index];
          if (!this.likeSong[index]) {
            prompt("取消收藏成功~");
          } else {
            prompt("收藏成功~");
          }
        }
        }
      );
    },
    LikeCommentOfplayList(index, id) {
      //封装post的对象
      this.likeCommentObj.comment_id = id;
      let list = this.likeList;
      this.likeCommentObj.type = list.indexOf(id) == -1 ? 1 : 2;
      console.log(this.likeCommentObj);

      axios.post("/itmusic/comment_like/playlist", this.likeCommentObj).then((res) => {
        if (res.data.code == "404 NOT FOUND.") {
          prompt("请先登录！");
        } else {
          this.likeComment[index] = !this.likeComment[index];
          let list = this.likeList;
          if (list.indexOf(id) == -1) {
            prompt("点赞成功");
            this.likeList.push(id);
            console.log(this.likeList);
          } else {
            for (var i in this.likeList) {
              if (this.likeList == id) {
