// Reload method
Vue.prototype.$reload = function() {
    this.$router.go(this.$router.currentRoute);
};

// Trim method
Vue.prototype.$trim = function(str) {
    return str.toString().replace(/(\r\n|\r|\n)+$/, '');
};

// Fetcher class
class Fetcher {
    constructor() {
        // Fuck media resources, I have to change the proxy for it.
        // Still in use for picture rendering.
        this.mediaUrl = 'http://47.102.215.61/media';
    }

    async account() {
        const res = await fetch('/itmusic/user/account');
        const data = await res.json();

        return data;
    }

    async id() {
        const res = await fetch('/itmusic/user/account');
        const data = await res.json();

        return data.user.id;
    }

    async playlists() {
        const res = await fetch('/itmusic/user/account');
        const data = await res.json();

        return data.create_lists;
    }

    async logout() {
        const res = await fetch('/itmusic/logout');
        const data = await res.json();

        return data;
    }

    async search(keywords) {
        const songRes = await fetch(`/itmusic/search?keywords=${keywords}&type=1`);
        const songData = await songRes.json();

        const albumRes = await fetch(`/itmusic/search?keywords=${keywords}&type=2`);
        const albumData = await albumRes.json();

        const playlistRes = await fetch(
            `/itmusic/search?keywords=${keywords}&type=3`
        );
        const playlistData = await playlistRes.json();

        const userRes = await fetch(`/itmusic/search?keywords=${keywords}&type=4`);
        const userData = await userRes.json();

        return {
            song: songData.search_song,

            album: albumData.search_album,

            playlist: playlistData.search_list,

            user: userData.search_user,
        };
    }

    async song(id) {
        const res = await fetch(`/itmusic/song/detail?id=${id}`);
        const data = await res.json();

        return data.song;
    }

    async album(id) {
        const res = await fetch(`/itmusic/album/detail?id=${id}`);
        const data = await res.json();

        return data.album;
    }

    async comments(id, page) {
        const res = await fetch(`/itmusic/comment/song?id=${id}&page=${page}`);
        const data = await res.json();

        return data;
    }

    async playlist(id) {
        const res = await fetch(`/itmusic/playlist/detail?id=${id}`);
        const data = await res.json();

        return data.list;
    }

    async user(id) {
        const res = await fetch(`/itmusic/user/detail?id=${id}`);
        const data = await res.json();

        return data.user;
    }

    async usersongs(id, page) {
        const res = await fetch(`/itmusic/user/songs?id=${id}&page=${page}`);
        const data = await res.json();

        return data;
    }

    async useralbums(id, page) {
        const res = await fetch(`/itmusic/user/albums?id=${id}&page=${page}`);
        const data = await res.json();

        return data;
    }

    async userplaylists(id, page, type) {
        const res = await fetch(`/itmusic/user/playlists?id=${id}&page=${page}&type=${type}`);
        const data = await res.json();

        return data;
    }
}

Vue.prototype.$fetcher = new Fetcher();

// Poster class
class Poster {
    // constructor() {
    //   this.hostUrl = 'http://127.0.0.1:4000';
    //   this.baseUrl = 'http://47.102.215.61/itmusic';
    //   this.mediaUrl = 'http://47.102.215.61/media';
    // }

    async signin(username, password) {
        const res = await fetch('/itmusic/login', {
            method: 'POST',
            body: JSON.stringify({
                username: username,
                password: password,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async send(username, password1, password2, email) {
        const res = await fetch('/itmusic/register', {
            method: 'POST',
            body: JSON.stringify({
                username: username,
                password1: password1,
                password2: password2,
                email: email,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async verify(code) {
        const res = await fetch('/itmusic/verify', {
            method: 'POST',
            body: JSON.stringify({
                email_code: code,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async likeSong(id, type) {
        const res = await fetch('/itmusic/collect_song', {
            method: 'POST',
            body: JSON.stringify({
                song_id: id,
                type: type,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async likeAlbum(id, type) {
        const res = await fetch('/itmusic/collect_album', {
            method: 'POST',
            body: JSON.stringify({
                album_id: id,
                type: type,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async likePlaylist(id, type) {
        const res = await fetch('/itmusic/collect_playlist', {
            method: 'POST',
            body: JSON.stringify({
                list_id: id,
                type: type,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async createList(name, description, picture) {
        const form = new FormData();

        form.append('list_name', name);
        form.append('list_description', description);
        form.append('list_picture', picture);

        const res = await fetch('/itmusic/create_list', {
            method: 'POST',
            body: form,
        });
        const data = await res.json();

        return data;
    }

    async deleteList(id) {
        const res = await fetch('/itmusic/delete_list', {
            method: 'POST',
            body: JSON.stringify({
                list_id: id,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async addToList(songId, listId, type) {
        const res = await fetch('/itmusic/addin_playlist', {
            method: 'POST',
            body: JSON.stringify({
                song_id: songId,
                list_id: listId,
                type: type,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async likeComment(id, type) {
        const res = await fetch('/itmusic/comment_like/song', {
            method: 'POST',
            body: JSON.stringify({
                comment_id: id,
                type: type,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async commentSong(id, content) {
        const res = await fetch('/itmusic/comment/song', {
            method: 'POST',
            body: JSON.stringify({
                song_id: id,
                content: content,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async uncommentSong(id) {
        const res = await fetch('/itmusic/delete_comment/song', {
            method: 'POST',
            body: JSON.stringify({
                comment_id: id,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }

    async changeProfile(username, age, gender, address, description, avatar) {
        const form = new FormData();

        form.append('username', username);
        form.append('age', age);
        form.append('gender', gender);
        form.append('address', address);
        form.append('description', description);
        form.append('avatar', avatar);

        const res = await fetch('/itmusic/userinfo', {
            method: 'POST',
            body: form,
        });
        const data = await res.json();

        return data;
    }

    async changePassword(oldPassword, newPassword, confirmPassword) {
        const res = await fetch('/itmusic/change_password', {
            method: 'POST',
            body: JSON.stringify({
                old_password: oldPassword,
                password1: newPassword,
                password2: confirmPassword,
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await res.json();

        return data;
    }
}

Vue.prototype.$poster = new Poster();

new Vue({
    router,
    render: (h) => h(App),
}).$mount('#app');